.progress-wrapper {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 0px;
    width: 100%;
    text-align: left;
}

.progress-background-bar {
    background: rgba(35, 51, 75, 0.3);
    display: block;
    width: 100%;
    height: 11px;
    border-color: #808080;
    border-radius: 5px 5px 5px 5px;
}

.progress-bar {
    background: rgba(27, 36, 51, 0.6);
    display: block;
    width: 0%;
    height: 11px;
    border-radius: 5px 5px 5px 5px;
    transition: max-height 0.25s ease-in
}