/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, button, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  -webkit-overflow-scrolling: touch; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-rendering: optimizeLegibility; }

*:focus {
  outline: none; }

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: 'Roboto';
  font-weight: 300; }

ul, li {
  margin: 0;
  padding: 0;
  list-style: none; }

h5 {
  font-size: 15px;
  font-weight: 700;
  color: white;
  text-align: center; }

.background-cover {
  background-size: cover;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #e5e7ed; }

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  box-shadow: 0px 0px; }

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
  -webkit-appearance: none;
  border-radius: 0; }

* {
  box-sizing: border-box; }

.green-button {
  color: #f8f8f8;
  cursor: pointer;
  display: block;
  height: 60px;
  max-width: 90%;
  min-height: 0px;
  min-width: 125px;
  width: 90%;
  background: #36c060;
  border: 0px none #f8f8f8;
  border-radius: 100px 100px 100px 100px;
  font-size: 18px;
  font-weight: 700;
  margin: 20px 0px 15px;
  padding: 0px;
  margin-left: 5%;
  box-sizing: border-box;
  font-family: "Roboto"; }

.icon-style {
  text-align: left;
  position: absolute;
  margin-left: 20px;
  color: white;
  z-index: 200; }

@media screen and (max-width: 679px) {
  .container {
    box-sizing: border-box;
    width: 90%;
    margin-left: 5%;
    margin-top: 12%;
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 4%;
    padding-right: 4%;
    max-height: 100%;
    max-width: 810px;
    position: relative;
    border: 0px none #1b2433;
    border-radius: 10px 10px 10px 10px;
    background: rgba(35, 51, 75, 0.7); }
  .title {
    text-align: center;
    color: white;
    height: 20px;
    border: 0px none white;
    font-weight: 700;
    font-size: 18px;
    margin: 0px 0px 20px; }
  .button-container {
    height: 47px;
    margin-top: 30px; } }

@media screen and (min-width: 680px) {
  .container {
    margin-top: 5%;
    box-sizing: border-box;
    width: 50%;
    margin-left: 25%;
    padding-top: 45px;
    padding-bottom: 45px;
    padding-left: 8%;
    padding-right: 8%;
    max-width: 810px;
    max-height: 100%;
    position: relative;
    text-decoration: none solid #1b2433;
    text-size-adjust: 100%;
    background: rgba(35, 51, 75, 0.7);
    border: 0px none #1b2433;
    border-radius: 10px 10px 10px 10px; }
  .title {
    box-sizing: border-box;
    color: white;
    height: 26px;
    font-weight: 700;
    margin: 0px 0px 20px;
    text-align: center;
    font-size: 24px; }
  .button-container {
    height: 47px;
    margin-top: 30px; } }

.back-button {
  color: #f8f8f8;
  cursor: pointer;
  display: block;
  height: 47px;
  max-height: 1 e6px;
  min-height: 0px;
  min-width: 125px;
  text-size-adjust: 100%;
  width: 130px;
  background: #36c060;
  border: 0px none #f8f8f8;
  border-radius: 100px 100px 100px 100px;
  font-weight: 700;
  font-size: 16px;
  padding: 0px;
  margin-top: 30px;
  box-sizing: border-box;
  float: left; }

.next {
  float: right; }

.question-number {
  color: white;
  font-weight: 500;
  font-size: 14px;
  margin: 0px 0px 14px;
  text-align: left; }

@media screen and (max-width: 680px) {
  .question-text {
    color: white;
    font-weight: 500;
    font-size: 22px;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left; } }

@media screen and (min-width: 680px) {
  .question-text {
    color: white;
    font-weight: 500;
    font-size: 22px;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left; } }
