/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, button, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  -webkit-overflow-scrolling: touch; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.summary {
  color: white;
  font-weight: 700;
  font-size: 22px;
  margin-top: 0px;
  margin-bottom: 20px;
  text-align: left; }

.summary-question {
  color: white;
  font-weight: 500;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: left; }

.summary-answer {
  color: white;
  font-weight: 500;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: left; }

@media screen and (min-width: 680px) {
  .questions-and-anwers {
    bottom: 0px;
    box-sizing: border-box;
    color: white;
    cursor: pointer;
    left: 0px;
    position: relative;
    right: 0px;
    top: 15px;
    width: 100%;
    background: rgba(27, 36, 51, 0.6);
    border: 0px none;
    border-radius: 5px 5px 5px 5px;
    font-weight: 700;
    font-size: 16px;
    margin: 0px 0px 5px;
    padding: 25px 50px;
    padding-left: 30px;
    border-color: rgba(27, 36, 51, 0.6); } }

@media screen and (max-width: 679px) {
  .questions-and-anwers {
    bottom: 0px;
    box-sizing: border-box;
    color: white;
    cursor: pointer;
    left: 0px;
    position: relative;
    right: 0px;
    top: 15px;
    width: 100%;
    background: rgba(27, 36, 51, 0.6);
    border: 0px none;
    border-radius: 5px 5px 5px 5px;
    font-weight: 700;
    font-size: 16px;
    margin: 0px 0px 5px;
    padding: 15px 50px;
    padding-left: 30px;
    border-color: rgba(27, 36, 51, 0.6); } }
